import { useState, useEffect, useCallback } from 'react';

import { useSelector, useDispatch } from 'react-redux';
import { setModulesSelectedUp, setModulesSelectedDown, setCurrentModuleSelected, setCurrentModuleSelectedListNum, setModulesCommon } from '../../../actions';

import { DndProvider } from 'react-dnd'
import { HTML5Backend } from 'react-dnd-html5-backend';

import { v4 as uuidv4 } from 'uuid';

import ListModulesSelectedItem from './../../listModulesItem/listModulesSelectedItem/ListModulesSelectedItem';
import ListModulesAddItem from './../../listModulesItem/listModulesAddItem/ListModulesAddItem';

import ListModulesPartUp1 from './../../listModulesPart/listModulesPartUp1/ListModulesPartUp1';
// import ListModulesPartUp2 from './../../listModulesPart/listModulesPartUp2/ListModulesPartUp2';
import ListModulesPartUp3 from './../../listModulesPart/listModulesPartUp3/ListModulesPartUp3';
import ListModulesPartDown from './../../listModulesPart/listModulesPartDown/ListModulesPartDown';

import pImg01 from './../img01.svg';
import pImg02 from './../img02.svg';
import pImg022 from './../img022.svg';
import pImg03 from './../img03.svg';
import pImg04 from './../img04.svg';
import pImg05 from './../img05.svg';
import pImg06 from './../img06.svg';
import pImg07 from './../img07.svg';
import pImg08 from './../img08.svg';
import pImg01Active from './../img01-active.svg';
import pImg02Active from './../img02-active.svg';
import pImg022Active from './../img022-active.svg';
import pImg03Active from './../img03-active.svg';
import pImg04Active from './../img04-active.svg';
import pImg05Active from './../img05-active.svg';
import pImg06Active from './../img06-active.svg';
import pImg07Active from './../img07-active.svg';
import pImg08Active from './../img08-active.svg';

const ListModulesSelected = () => {
  const { modulesSelectedUp, modulesSelectedDown, currentModuleSelected, width, currentModuleSelectedListNum, height, depth, modulesSelectedMiddle} = useSelector(state => state);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(setModulesCommon([
      {
        'startId': 1,
        'id': 1,
        'quanity': 0,
        'name': 'Верхний шкаф',
        'text': 'Одна дверь',
        'img': pImg01,
        'activeImg': pImg01Active,

        'type': 'up',
        'width': 40,
        'appointment': 'storage',

        'indent': 0
      },
      // {
      //   'id': 2,
      //   'quanity': 0,
      //   'name': 'Верхний ящик',
      //   'text': 'Одна дверь',
      //   'img': pImg01,
      //   'activeImg': pImg01Active,

      //   'type': 'up',
      //   'width': 40,
      //   'appointment': 'storage',

      //   'indent': 0,
      // },
      {
        'startId': 2,
        'id': 2,
        'quanity': 0,
        'name': 'Нижний шкаф',
        'text': 'Одна дверь',
        'img': pImg02,
        'activeImg': pImg02Active,

        'type': 'down',
        'width': 40,
        'appointment': 'storage',

        'replaceMax': 3,
        'indent': 0,
      },
      {
        'startId': 3,
        'id': 3,
        'quanity': 0,
        'name': 'Нижний шкаф',
        'text': 'Двойная дверь',
        'img': pImg06,
        'activeImg': pImg06Active,

        'type': 'down',
        'width': 60,
        'appointment': 'storage',

        'replaceMin': 2,
        'indent': 0,
      },
      {
        'startId': 4,
        'id': 4,
        'quanity': 0,
        'name': 'Нижняя угловая',
        'text': 'Левый',
        'img': pImg07,
        'activeImg': pImg07Active,

        'type': 'down',
        'width': 60,
        'appointment': 'storage',

        'indent': 0,
      },
      {
        'startId': 5,
        'id': 5,
        'quanity': 0,
        'name': 'Нижняя угловая',
        'text': 'правый',
        'img': pImg08,
        'activeImg': pImg08Active,

        'type': 'down',
        'width': 60,
        'appointment': 'storage',

        'indent': 0,
      },
      {
        'startId': 7,
        'id': 7,
        'quanity': 0,
        'name': 'Под духовку',
        'text': 'Описание',
        'img': pImg03,
        'activeImg': pImg03Active,

        'type': 'down',
        'width': 80,
        'appointment': 'technic',

        'indent': 24,
      },
      {
        'startId': 8,
        'id': 8,
        'quanity': 0,
        'name': 'Под мойку',
        'text': 'Описание',
        'img': pImg04,
        'activeImg': pImg04Active,

        'type': 'down',
        'width': 80,
        'appointment': 'washing',

        'indent': 39,
      },
    ]))
  }, [])

  const onAddModulesSelectedUp = () => {
    const id = uuidv4();
    const item = {
      id, 
      type: 'up',
      depthType: 'full', 
      mounted: 'x1',
      width: 600,
      height: 800,
      depth,
      modeles: {
        // up1: {},
        up3: {
          startId: 2,
          id: 2,
          img: pImg02,
          activeImg: pImg02Active,
          replaceMax: 3,
          replaceMin: null,
          indent: 0,
          appointment: 'storage',
        }
      }
    }
    dispatch(setModulesSelectedUp([
      ...modulesSelectedUp, item
    ]));
    dispatch(setCurrentModuleSelected(item));
    dispatch(setCurrentModuleSelectedListNum(1));
  }

  const onAddModulesSelectedDown = () => {
    const id = uuidv4();
    const item = {
      id, 
      type: 'down',
      width: 600,
      height: 800,
      depth,
      modeles: {
        down: {
          startId: 2,
          id: 2,
          img: pImg022,
          activeImg: pImg022Active,
          replaceMax: 3,
          replaceMin: null,
          indent: 0,
          appointment: 'storage', 
        },
      },
      shelves: 0,
      removableShelves: false,
      material: false,
      backWall: false,
      front: false,
      edge: false,
      ties: 80, 
    }
    dispatch(setModulesSelectedDown([
      ...modulesSelectedDown, item
    ]));
    dispatch(setCurrentModuleSelected(item));
    dispatch(setCurrentModuleSelectedListNum(1));
  }

  const onAddModulesSelectedMedium = () => {
    const id = uuidv4();
    const id2 = uuidv4();
    const up = {
      id,
      type: 'middle-up',
      depthType: 'full', 
      mounted: 'x1',
      width: 400,
      modeles: {}
    }
    const down = {
      id: id2, 
      type: 'middle',
      width: 400,
      height: height,
      depth,
      modeles: {
        down: {},
      },
      shelves: 0,
      removableShelves: false,
      material: false,
      backWall: false,
    }
    dispatch(setModulesSelectedUp([
      ...modulesSelectedUp, up
    ]));
    dispatch(setModulesSelectedDown([
      ...modulesSelectedDown, down
    ]));
    dispatch(setCurrentModuleSelected(down));
    dispatch(setCurrentModuleSelectedListNum(1));
  }

  const moveModulesSelectedUp = useCallback(
    (dragIndex, hoverIndex) => {
      const dragItem = modulesSelectedUp[dragIndex]
      const hoverItem = modulesSelectedUp[hoverIndex]

      const updatedModules = [...modulesSelectedUp];
      updatedModules[dragIndex] = hoverItem;
      updatedModules[hoverIndex] = dragItem;

      dispatch(setModulesSelectedUp(updatedModules))
    },
    [modulesSelectedUp],
  )

  const renderSelectedUpItems = (items) => {
    return items.map((item, index) => {
      // const up2View = item.modeles.up2 !== false ? <ListModulesPartUp2 item={item}/> : null;
      const up2View = null;
      const up1View = item.modeles.up1 !== false ? <ListModulesPartUp1 item={item} width={up2View} /> : null;
      const upRowView = up1View || up2View ? <div className='configuration-layout-plan-selected__up'>{up1View}{up2View}</div> : null;
      const up3View = item.modeles.up3 !== false ? <ListModulesPartUp3 width={up1View} item={item}/> : null;
      return (
        <ListModulesSelectedItem
          key={item.id}
          height={up1View}
          index={index}
          el={item}
          setT={setT}
          cName='configuration-layout-plan-selected__item-w-up'
          moveModulesSelected={moveModulesSelectedUp}
          listNum={1}>
          {upRowView}
          {up3View}
        </ListModulesSelectedItem>
      )
    });
  }

  const [t, setT] = useState(false);

  const moveModulesSelectedDown = useCallback(
    (dragIndex, hoverIndex) => {
      const dragItem = modulesSelectedDown[dragIndex]
      const hoverItem = modulesSelectedDown[hoverIndex]

      const updatedModules = [...modulesSelectedDown];
      updatedModules[dragIndex] = hoverItem;
      updatedModules[hoverIndex] = dragItem;

      if (dragIndex !== hoverIndex && t === 'middle') {
        const dragItem2 = modulesSelectedUp[dragIndex]
        const hoverItem2 = modulesSelectedUp[hoverIndex]
    
        const updatedModules2 = [...modulesSelectedUp];
        updatedModules2[dragIndex] = hoverItem2;
        updatedModules2[hoverIndex] = dragItem2;
        dispatch(setModulesSelectedUp(updatedModules2));
      }
      dispatch(setModulesSelectedDown(updatedModules))
    },
    [modulesSelectedDown],
  )

  const moveModulesSelectedMiddle = useCallback(
    (dragIndex, hoverIndex) => {
      const dragItem = modulesSelectedDown[dragIndex]
      const hoverItem = modulesSelectedDown[hoverIndex]

      const updatedModules = [...modulesSelectedDown];
      updatedModules[dragIndex] = hoverItem;
      updatedModules[hoverIndex] = dragItem;

      const dragItem2 = modulesSelectedUp[dragIndex]
      const hoverItem2 = modulesSelectedUp[hoverIndex]

      const updatedModules2 = [...modulesSelectedUp];
      updatedModules2[dragIndex] = hoverItem2;
      updatedModules2[hoverIndex] = dragItem2;

      dispatch(setModulesSelectedDown(updatedModules));
      dispatch(setModulesSelectedUp(updatedModules2));

    },
    [modulesSelectedDown, modulesSelectedUp],
  )

  const renderSelectedDownItems = (items) => {
    return items.map((item, index) => {
      const down3View = item.modeles.down !== false ? <ListModulesPartDown item={item}/> : null;
      return (
        <ListModulesSelectedItem
          key={item.id} 
          index={index}
          el={item}
          setT={setT}
          cName='configuration-layout-plan-selected__item-w-down'
          moveModulesSelected={item.type==='middle'?moveModulesSelectedMiddle:moveModulesSelectedDown}
          listNum={1}>
          {down3View}
        </ListModulesSelectedItem>
      )
    });
  }

  const selectedItemsUp = renderSelectedUpItems(modulesSelectedUp);
  const selectedItemsDown = renderSelectedDownItems(modulesSelectedDown);

  const calсWidth = (arr) => {
    let w = 0;
    if (modulesSelectedMiddle.length > 1) {
      modulesSelectedMiddle.forEach((item) => {
        if (item.content !== 'body') {
          w += item.width;
        }
      });
    }
    arr.forEach((item) => {
      w += item.width;
    });
    if (w+400 > width) 
      return false;
    return true;
  }

  // const renderAddItemMiddle = () => {
  //   if (modulesSelectedUp.length !== modulesSelectedDown.length)
  //     return null;
  //   return <ListModulesAddItem 
  //             middle={null}
  //             classNameType={'middle'} 
  //             onAddModulesSelected={onAddModulesSelectedMedium} />
  // }

  // const addItemMiddle = renderAddItemMiddle();

  const addItemUp = calсWidth(modulesSelectedUp) ?
                     <ListModulesAddItem 
                      middle={null}
                      classNameType={'up'} 
                      onAddModulesSelected={onAddModulesSelectedUp} /> 
                     : null;

  const addItemDown = calсWidth(modulesSelectedDown) ?
                     <ListModulesAddItem 
                      middle={null}
                      classNameType={'down'} 
                      onAddModulesSelected={onAddModulesSelectedDown} />
                     : null;



  return (
    <>
      <div
        className="configuration-layout-plan__selected configuration-layout-plan-selected">
        <DndProvider backend={HTML5Backend}>
          {selectedItemsUp}
        </DndProvider>
        {addItemUp}
      </div>
      <div 
        className="configuration-layout-plan__selected configuration-layout-plan-selected">
        {/* <div className={`resize-h ${currentModuleSelected?.height && currentModuleSelectedListNum === 1 ? 'active':''}`}>
            <span></span>
            <span>{currentModuleSelected.height} мм</span>
            <span></span>
        </div> */}
        <DndProvider backend={HTML5Backend}>
          {selectedItemsDown}
        </DndProvider>
        {addItemDown}
      </div>
    </>
  );
};

export default ListModulesSelected;