import { useState, useCallback, useRef, useEffect } from 'react';

import { useSelector, useDispatch } from 'react-redux';
import { setCurrentModuleSelected, setCurrentModuleSelectedListNum, setModulesSelectedMiddle } from './../../actions/index';

import { DndProvider, useDrag, useDrop } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';

import { v4 as uuidv4 } from 'uuid';

import useCalc from './../../service/Calc';

import WidthPlan from './../widthPlan/WidthPlan';
import ListModulesSelected from './../listModules/listModulesSelected/ListModulesSelected';
import ListModulesSelected2 from './../listModules/listModulesSelected2/ListModulesSelected2';

// import ListModulesSelectedItem from './../listModulesItem/listModulesSelectedItem/ListModulesSelectedItem';
// import ListModulesPartMiddle from './../listModulesPart/listModulesPartMedium/ListModulesPartMiddle';
import ListModulesPartResize from './../listModulesPart/listModulesPartResize/ListModulesPartResize';

// import { pImg05, pImg05Active } from '../../resources/img/configurationLayout';
import pImg05 from './../listModules/img05.svg';
import pImg05Active from './../listModules/img05-active.svg';
import ListModulesPartMiddle from '../listModulesPart/listModulesPartMiddle/ListModulesPartMiddle';

const ConfigurationMap = ({ sizeWidth }) => {
  const {configuration, wall, height, depth, modulesSelectedMiddle} = useSelector(state => state);
  const dispatch = useDispatch();

  // const [medium, setMedium] = useState([]);  

  useEffect(() => {
    const middle = {
      id: uuidv4(), 
      type: 'middle',
      width: 600,
      height: height,
      depth,
      modeles: {
        middle: {
          startId: 2,
          id: 2,
          img: pImg05,
          activeImg: pImg05Active,
          replaceMax: null,
          replaceMin: null,
          indent: 0,
          appointment: 'storage', 
        },
      },
      shelves: 0,
      removableShelves: false,
      material: false,
      backWall: false,
    }
    const content = {id: 1, content: 'body', type: 'middle'};
    // setMedium([middle, content]);
    dispatch(setModulesSelectedMiddle([middle, content]))
  }, [])

  const moveModulesSelectedMiddle = useCallback(
    (dragIndex, hoverIndex) => {
      const dragItem = modulesSelectedMiddle[dragIndex]
      const hoverItem = modulesSelectedMiddle[hoverIndex]

      const updatedModules = [...modulesSelectedMiddle];
      updatedModules[dragIndex] = hoverItem;
      updatedModules[hoverIndex] = dragItem;

      dispatch(setModulesSelectedMiddle(updatedModules));
    }, 
    [modulesSelectedMiddle],
  )

  const renderBody = () => {
    // console.log('ModulesSelectedMiddle:', modulesSelectedMiddle);
    return modulesSelectedMiddle.map((item, i) => {
      if (item.content === 'body') {
        return <MiddleItem
            key={item.id}
            el={item}
            index={i}
            noMove={(e) => e.preventDefault()}
            func={moveModulesSelectedMiddle}
          >{null}</MiddleItem> 
      } else {
        const m = item.modeles.down !== false ? <ListModulesPartMiddle item={item}/> : null;
        return (
          <MiddleItem 
            key={item.id}
            el={item}
            index={i}
            noMove={null} 
            func={moveModulesSelectedMiddle}
          >{m}</MiddleItem> 
      )}
    });
  }

  const bodyView = renderBody();

  const configurationView = wall === 1 ? 
      <div
        style={{width: `calc(${sizeWidth}% + 12px`}}
        className="configuration-layout-plan__wrapper-modeules">
        <DndProvider backend={HTML5Backend}>
          {bodyView}
        </DndProvider>
      </div> 
    : 
    <div
      style={{width: `calc(${sizeWidth}% + 12px`}}
      className="configuration-layout-plan__wrapper-modeules">
      <DndProvider backend={HTML5Backend}>
        <ListModulesSelected2 />
      </DndProvider>
    </div>;

  const renderMap = () => {
    switch (configuration) {
      case 1:
        return (
          <>
            <div style={{height: '50px'}}></div>
            <WidthPlan />
            <div
              style={{width: `calc(${sizeWidth}% + 12px`, display: 'flex'}}
              // style={{width: `240px`, display: 'flex'}}
              className="configuration-layout-plan__wrapper-modeules">
              <DndProvider backend={HTML5Backend}>
                {bodyView}
              </DndProvider>
            </div>
            <div style={{height: '35px'}}></div>
          </>
        )
      case 2:
        return (
          <>
            <div style={{height: '50px'}}></div>
            <WidthPlan />
            {configurationView}
            <div style={{height: '50px'}}></div>
          </>
        )
      case 3:
        return (
          <>
            <div style={{height: '50px'}}></div>
            <WidthPlan />
            {configurationView}
            <div style={{height: '50px'}}></div>
          </>
        )
      case 4:
        return (
          <>
            <div style={{height: '50px'}}></div>
            <WidthPlan />
            <div
              style={{width: `calc(${sizeWidth}% + 12px`}}
              className="configuration-layout-plan__wrapper-modeules">
              <DndProvider backend={HTML5Backend}>
                <ListModulesSelected />
              </DndProvider>
            </div>
            <div style={{height: '50px'}}></div>
          </>
        )
      default:
        return null;
    }
  }

  return renderMap();
};

const MiddleItem = ({el, index, noMove, func, listNum, children}) => {
  const { currentModuleSelected, height } = useSelector(state => state);
  const dispatch = useDispatch();

  const {calcWidthPercentItem} = useCalc();

  const [{ isDragging }, dragRef] = useDrag({
    type: 'configuration-layout-plan-selected__item-w-middle-e',
    item: { index },
    collect: (monitor) => ({
      isDragging: monitor.isDragging(),
    }),
  })

  const [spec, dropRef] = useDrop({
    accept: 'configuration-layout-plan-selected__item-w-middle-e',
    hover: (item, monitor) => {
      const dragIndex = item.index
      const hoverIndex = index
      const hoverBoundingRect = ref.current?.getBoundingClientRect()
      const hoverMiddleY = (hoverBoundingRect.right - hoverBoundingRect.left) / 2
      const hoverActualY = monitor.getClientOffset().x - hoverBoundingRect.left

      if (dragIndex < hoverIndex && hoverActualY < hoverMiddleY) return
      if (dragIndex > hoverIndex && hoverActualY > hoverMiddleY) return

      func(dragIndex, hoverIndex);
      item.index = hoverIndex;
    },
  })

  const ref = useRef(null)
  const dragDropRef = dragRef(dropRef(ref))

  const opacity = isDragging ? 0 : 1


  if (el.content === 'body')
    return (
      <div 
        ref={dragDropRef}
        onMouseDown={(e) => {
          if (e.target.classList.contains('configuration-layout-plan-selected__item-w-middle-e'))
            e.preventDefault();
        }}
        className="configuration-layout-plan-selected__item-w-middle-e">
        <ListModulesSelected/>
      </div>
    )

  const calcWidth = calcWidthPercentItem(el.width);

  console.log(calcWidth);

  const onClickItem = (el) => {
    dispatch(setCurrentModuleSelectedListNum(listNum));
    dispatch(setCurrentModuleSelected(el));
  } 

  return (
    <div
      onClick={() => onClickItem(el, listNum)}
      ref={dragDropRef}
      style={{  
        boxSizing: 'content-box',
        padding: '0 5px',
        height: height, 
        opacity: opacity,
        width: `${calcWidth}px`,
        background: opacity ? '' : '#000' }}
      className={`configuration-layout-plan-selected__item-w-middle-e ${el.id === currentModuleSelected?.id ? 'active':''}`}>
      {children}
      {/* <ListModulesPartResize item={el}/> */}
    </div>
  )
}

export default ConfigurationMap;